import {api} from '/js/api';
import {Languages, request , url} from '/js/util';

api({
	getMain: 'Information/GetInformationSubjectById',
	getSub: 'Information/InformationById',
});

const informationId = request['id'];
const comepage=request['comepage'];



if(request['type']==2){
	$('.mui-title').text(Languages.package.sponsor);
	$('#aBack').attr('href', url.setParam(__uri('./list.html') , {type:request['type']}));
}




$.when(
	api.getMain({id: informationId} , data=>{
		document.getElementById('main').innerHTML = template('tpl-main', data[0]);
	}),
).then(()=>{
	api.getSub({informationId} , data=>{
		if(data.length){
			let html = '';
			data.forEach(v=>{
				html += `<p>${v.content}</p>`
			});
			document.getElementById('sub').innerHTML = html;
		}
	})
});

$('#aBack').click(function () {
	if(comepage==="dashboard")
    	location.href = '../dashboard/index.html';
    else if(comepage==="newslist")
    	location.href= 'list.html';
    else
        location.href = '../dashboard/index.html';
});
